import React, { Component } from 'react'

export default class Experience extends Component {
  render() {
    return (
        <div>
            <section className="colorlib-experience" data-section="experience">
            <div className="colorlib-narrow-content">
                <div className="row">
                <div className="col-md-6 col-md-offset-3 col-md-pull-3 animate-box" data-animate-effect="fadeInLeft">
                    <span className="heading-meta">Cronograma Experiencia</span>
                    <h2 className="colorlib-heading animate-box">Experiencia laboral</h2>
                </div>
                </div>
                <div className="row">
                <div className="col-md-12">
                    <div className="timeline-centered">
                    <article className="timeline-entry animate-box" data-animate-effect="fadeInLeft">
                        <div className="timeline-entry-inner">
                        <div className="timeline-icon color-3">
                            <i className="icon-pen2" />
                        </div>
                        <div className="timeline-label">
                            <h2><a href="https://www.linkedin.com/company/capgemini/about/" target="_blank" rel="noopener noreferrer">Capgemini <span>sep. 2020 - Presente</span></a></h2>
                            <p>A día de hoy trabajo en Capgemini España, en el área de CSD (Custom Software Development), concretamente en el equipo de Innovación. Principalmente llevo a cabo tareas relacionadas con el análisis de nuevas tecnologías y el desarrollo de apliciaciones multiplataforma en C#, la cuales incluyan dichas tecnologías.</p>
                        </div>
                        </div>
                    </article>
                    <article className="timeline-entry animate-box" data-animate-effect="fadeInLeft">
                        <div className="timeline-entry-inner">
                        <div className="timeline-icon color-3">
                            <i className="icon-pen2" />
                        </div>
                        <div className="timeline-label">
                            <h2><a href="https://www.linkedin.com/company/capgemini/about/" target="_blank" rel="noopener noreferrer">Capgemini <span>nov. 2019 - jul. 2020</span></a></h2>
                            <p>A día de hoy trabajo en Capgemini España, en el área de CSD (Custom Software Development), concretamente en el equipo de Innovación. Principalmente llevo a cabo tareas relacionadas con el análisis de nuevas tecnologías y el desarrollo de apliciaciones multiplataforma en C#, la cuales incluyan dichas tecnologías.</p>
                        </div>
                        </div>
                    </article>
                    <article className="timeline-entry animate-box" data-animate-effect="fadeInTop">
                        <div className="timeline-entry-inner">
                        <div className="timeline-icon color-4">
                            <i className="icon-pen2" />
                        </div>
                        <div className="timeline-label">
                            <h2><a href="https://www.linkedin.com/company/emurtel/about/" target="_blank" rel="noopener noreferrer">Emurtel <span>nov. 2018 - feb. 2019</span></a></h2>
                            <p>La empresa Emurtel fue mi primer contacto con el mundo laboral por medio de las prácticas univiersitarias. Fue un periodo corto en el cual me dediqué principalmente a dos tareas: realizar la migración de un servidor y estudiar/realizar propuestas técnicas a los diferentes clientes.</p>
                        </div>
                        </div>
                    </article>
                    <article className="timeline-entry begin animate-box" data-animate-effect="fadeInBottom">
                        <div className="timeline-entry-inner">
                        <div className="timeline-icon color-none">
                        </div>
                        </div>
                    </article>
                    </div>
                </div>
                </div>
            </div>
            </section>
        </div>
    )
    }
}
