import React, { Component } from 'react'

export default class Timeline extends Component {
  render() {
    return (
      <div>
        <section className="colorlib-education" data-section="education">
          <div className="colorlib-narrow-content">
            <div className="row">
              <div className="col-md-6 col-md-offset-3 col-md-pull-3 animate-box" data-animate-effect="fadeInLeft">
                <span className="heading-meta">Cronograma Educación</span>
                <h2 className="colorlib-heading animate-box">Formación a destacar</h2>
              </div>
            </div>
            <div className="row">
              <div className="col-md-12">
                <div className="timeline-centered">
                  <article className="timeline-entry animate-box" data-animate-effect="fadeInLeft">
                    <div className="timeline-entry-inner">
                      <div className="timeline-icon color-3">
                        <i className="icon-pen2" />
                      </div>
                      <div className="timeline-label">
                        <h2>Master en ciberseguridad <span>2020 - Presente</span></h2>
                        <p>Tras finalizar la carrera, comencé un Master en ciberseguridad en la Universidad Nebrija, impartido por Deloitte. A día de hoy continúo cursándolo y aprendiendo cosas nuevas cada día.</p>
                      </div>
                    </div>
                  </article>
                  <article className="timeline-entry animate-box" data-animate-effect="fadeInTop">
                    <div className="timeline-entry-inner">
                      <div className="timeline-icon color-4">
                        <i className="icon-pen2" />
                      </div>
                      <div className="timeline-label">
                        <h2>Otras formaciones <span>2014 - Presente</span></h2>
                        <p>Durante los años que he estado cursando la carrera, de forma paralela y autodidacta, he ido haciendo otros cursos y formaciones. Las formaciones que he completado son los siguientes: <b>CCNA2</b> - Routing And Switching, Curso de desarrollo con <b>Android Studio</b>, Curso de desarrollo de videojuegos con <b>Unity 3D</b> y Curso básico de <b>hacking ético</b></p>
                      </div>
                    </div>
                  </article>
                  <article className="timeline-entry animate-box" data-animate-effect="fadeInLeft">
                    <div className="timeline-entry-inner">
                      <div className="timeline-icon color-5">
                        <i className="icon-pen2" />
                      </div>
                      <div className="timeline-label">
                        <h2>Grado en Ingeniería Informática <span>2014 - 2020</span></h2>
                        <p>Durante mi paso por la carrera desarrollé un especial interés por la ciberseguridad, provocando que más tarde continuara mi formación en este campo. Sin embargo, siempre me ha atraído el mundo de la programación y es por ello que tanto mi trabajo final de grado como mi actual trabajo están relacionados con este campo.</p> 
                      </div>
                    </div>
                  </article>
                  <article className="timeline-entry begin animate-box" data-animate-effect="fadeInBottom">
                    <div className="timeline-entry-inner">
                      <div className="timeline-icon color-none">
                      </div>
                    </div>
                  </article>
                </div>
              </div>
            </div>
          </div>
        </section>
      </div>
    )
  }
}
