import React, { Component } from 'react'
import Pdf from '../Documents/CV-OrtegaGomezRaul.pdf';

export default class Introduction extends Component {
  render() {
    return (
      <div>
        <section id="colorlib-hero" className="js-fullheight" data-section="home">
          <div className="flexslider js-fullheight">
            <ul className="slides">
              <li style={{backgroundImage: 'url(images/img_bg.jpg)'}}>
                <div className="overlay" />
                <div className="container-fluid">
                  <div className="row">
                    <div className="col-md-6 col-md-offset-3 col-md-pull-3 col-sm-12 col-xs-12 js-fullheight slider-text">
                      <div className="slider-text-inner js-fullheight">
                        <div className="desc">
                          <h1>Bienvenid@! <br />Soy Raúl Ortega Gómez</h1>
                          <p>
                            <a className="btn btn-primary btn-learn" href={Pdf} target="_blank" rel="noopener noreferrer">Ver CV<i className="icon-download4"/></a>
                          </p>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </li>
              <li style={{backgroundImage: 'url(images/img_bg.jpg)'}}>
                <div className="overlay" />
                <div className="container-fluid">
                  <div className="row">
                    <div className="col-md-6 col-md-offset-3 col-md-pull-3 col-sm-12 col-xs-12 js-fullheight slider-text">
                      <div className="slider-text-inner">
                        <div className="desc">
                          <h1>Echa un<br /> vistazo!</h1>
                          <p><a className="btn btn-primary btn-learn" href="https://github.com/rortegag" target="_blank" rel="noopener noreferrer">Ver Proyectos <i className="icon-briefcase3" /></a></p>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </li>
              <li style={{backgroundImage: 'url(images/img_bg.jpg)'}}>
                <div className="overlay" />
                <div className="container-fluid">
                  <div className="row">
                    <div className="col-md-6 col-md-offset-3 col-md-pull-3 col-sm-12 col-xs-12 js-fullheight slider-text">
                      <div className="slider-text-inner">
                        <div className="desc">
                          <h1>Búscame  <br/> en LinkedIn! </h1>
                          <p><a className="btn btn-primary btn-learn" href="https://www.linkedin.com/in/ra%C3%BAl-ortega-g%C3%B3mez/" target="_blank" rel="noopener noreferrer">Ver LinkedIn <i className="icon-linkedin2" /></a></p>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </li>
            </ul>
          </div>
        </section>
      </div>
    )
  }
}
